.consultation {
    padding-top: 100px;
    padding-bottom: 100px;

    &__wrapper {
        display: flex;
        justify-content: space-between;

        @media (max-width: 767px) {
            flex-direction: column;
        }
    }

    /* LEFT SIDE */

    &__info {
        margin-right: 15px;
        max-width: 310px;

        @media (max-width: 767px) {
            margin-right: initial;
            margin-bottom: 20px;
            max-width: initial;
        }
    }

    &__text {
        margin-top: 45px;
        line-height: 160%;

        @media (max-width: 992px) {
            margin-top: 15px;
        }
    }

    /* RIGHT SIDE */

    &__steps {
        border-radius: 10px;
        padding: 20px 60px 75px;
        box-shadow: 0 5px 20px rgba(27, 39, 63, 0.2);
        background-color: var(--clr-primary-500);

        &-progress {
            &-list {
                display: flex;
                justify-content: space-between;
                margin-bottom: 40px;

                @media (max-width: 992px) {
                    margin-bottom: 20px;
                }
            }

            &-item {
                border-radius: 10px;
                width: 100%;
                min-height: 5px;
                max-width: 160px;
                background-color: #4f8ae8;

                &--active {
                    background-color: var(--clr-primary-300);
                }
            }

            &-item + &-item {
                margin-left: 5px;
            }
        }

        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &-count {
            font-size: fluid-font(2.4rem, 1.8rem);
            line-height: 150%;
            color: var(--clr-primary-400);
        }

        &-current {
            color: var(--clr-default-100);
        }

        &-title {
            font-size: fluid-font(2.4rem, 2rem);
            line-height: 150%;
            color: var(--clr-default-100);
        }

        &-service {
            &-list {
                display: grid;
                justify-items: center;
                grid-gap: 25px;
                grid-template-columns: repeat(7, 1fr);
                margin-top: 40px;

                @media (max-width: 1200px) {
                    grid-gap: 15px;
                }

                @media (max-width: 992px) {
                    grid-template-columns: repeat(6, 1fr);
                    margin-top: 20px;
                }

                @media (max-width: 767px) {
                    grid-template-columns: repeat(7, 1fr);
                }

                @media (max-width: 576px) {
                    grid-gap: 5px;
                    grid-template-columns: repeat(5, 1fr);
                }

                @media (max-width: 460px) {
                    grid-template-columns: repeat(4, 1fr);
                }
            }

            &-item {
                position: relative;
                border: 4px solid transparent;
                border-radius: 10px;
                padding: 10px;
                cursor: pointer;

                &:not(.consultation__steps-service-item--active) {
                    @media (hover) {
                        &:hover {
                            border-color: var(--clr-primary-300);
                        }
                    }
                }

                &-img {
                    max-width: 44px;

                    img {
                        z-index: 5;
                        width: 100%;
                        height: auto;
                        object-fit: cover;
                    }
                }

                &--active {
                    border-color: var(--clr-primary-400);
                }

                @media (max-width: 992px) {
                    padding: 5px;
                }
            }
        }

        @media (max-width: 1100px) {
            padding: 20px 25px 35px;
        }

        @media (max-width: 767px) {
            padding: 20px 15px 25px;
        }
    }

    @media (max-width: 767px) {
        padding-top: 50px;
        padding-bottom: 50px;
    }
}