.section {
    &__title {
        font-weight: 700;
        font-size: fluid-font(3.6rem, 2.6rem);
        line-height: 100%;
        color: var(--clr-primary-500);
    }

    &__btn {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        padding-left: 15px;

        &-img {
            position: absolute;
            top: 50%;
            display: block;
            border-radius: 50%;
            transform: translateY(-50%);

            svg {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                transition: fill $ms;
            }
        }

        &--large {
            border-radius: 50px;
            padding-right: 15px;
            min-height: 60px;
            transition: background-color $ms, color $ms, outline $ms;

            .section__btn-img {
                left: 10px;
                width: 40px;
                height: 40px;
                background-color: var(--clr-default-100);
                transition: background-color $ms;

                svg {
                    fill: var(--clr-default-800);
                }
            }
        }

        &--small {
            border: 1px solid var(--clr-default-200);
            border-radius: 50px;
            padding-right: 40px;
            min-width: 185px;
            min-height: 40px;
            text-transform: uppercase;
            transition: background-color $ms, color $ms, border $ms;

            .section__btn-img {
                right: 10px;
                width: 25px;
                height: 25px;
                background-color: var(--clr-primary-300);
                transition: background-color $ms;

                svg {
                    fill: var(--clr-default-100);
                }
            }

            @media (hover) {
                &:hover {
                    border: 1px solid var(--clr-default-800);
                    transition: border $ms;
                }
            }
        }
    }
}