.reviews {
    padding-top: 85px;
    padding-bottom: 80px;
    background-color: var(--clr-default-800);

    @media (max-width: 767px) {
        padding-top: 40px;
        padding-bottom: 40px;
    }

    .section__title {
        color: var(--clr-default-200);

        @media (max-width: 576px) {
            max-width: 6ch;
        }
    }

    &__info {
        z-index: 1;
    }

    &__text {
        margin-top: 20px;
        max-width: 290px;
        line-height: 160%;
        color: var(--clr-default-100);

        @media (max-width: 767px) {
            margin-top: 15px;
        }

        p + p {
            margin-top: 10px;
        }
    }

    &__wrapper {
        position: relative;
        display: flex;
        justify-content: space-between;

        @media (max-width: 992px) {
            flex-direction: column;
        }
    }

    &__inner {
        position: relative;
        max-width: 570px;
        width: 100%;

        @media (max-width: 992px) {
            margin-top: 30px;
            max-width: initial;
        }
    }

    &__decor {
        max-width: 191px;
        position: absolute;
        top: 0;
        left: 38%;
        transform: translateX(-38%);
        z-index: 0;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }

        @media (max-width: 992px) {
            max-width: 140px;
            left: initial;
            right: 0;
            transform: initial;
        }

        @media (max-width: 576px) {
            max-width: 100px;
        }
    }

    /* Slider */

    &__slider {
        position: initial;
        margin-left: initial;
        margin-right: initial;
        border-radius: 10px;

        /* Slider BTNS */

        &-btns {
            position: absolute;
            bottom: 0;
            left: -60px;
            display: flex;
            align-items: center;

            @media (max-width: 992px) {
                position: initial;
                margin-left: 15px;
            }

            .swiper-button-next,
            .swiper-button-prev {
                position: initial;
                background-color: transparent;

                @media (max-width: 992px) {
                    background-color: var(--clr-primary-500);
                    border-radius: 50%;
                }

                @media (max-width: 576px) {
                    height: 45px;
                    width: 45px;
                }

                @media (max-width: 420px) {
                    height: 30px;
                    width: 30px;
                }
            }

            .swiper-button-disabled {
                background-color: transparent;
                box-shadow: initial;

                @media (max-width: 992px) {
                    background-color: #2B3E60;
                }
            }

            .swiper-button-next {
                @media (max-width: 992px) {
                    margin-left: 10px;
                }

                svg {
                    fill: var(--clr-default-800);

                    @media (max-width: 992px) {
                        fill: var(--clr-default-100);
                    }
                }
            }

            .swiper-button-prev {

                svg {
                    fill: var(--clr-default-100);
                }
            }
        }
    }

    &__slide {
        padding: 50px 40px;
        min-height: 345px;

        border-radius: 10px;
        background-color: var(--clr-default-100);
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.08);

        @media (max-width: 992px) {
            min-height: initial;
        }

        @media (max-width: 767px) {
            padding: 30px 20px;
        }

        &-top {
            display: flex;
            margin-bottom: 15px;
            position: relative;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                width: 30px;
                height: 30px;
                background-image: url('../img/reviews/eye.svg');
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
            }
        }

        &-title {
            margin-bottom: 8px;
            font-weight: 700;
        }

        &-text {
            font-size: 1.4rem;
            padding-left: 80px;
            line-height: 185%;

            @media (max-width: 992px) {
                padding-left: initial;
            }
        }

        &-img {
            max-width: 60px;
            margin-right: 20px;

            img {
                border-radius: 50%;
                width: 100%;
                height: auto;
                object-fit: cover;
            }
        }
    }

    /* Slider Preview */
    &__mini {
        &-slider {
            @media (max-width: 992px) {
                max-width: 365px;
                width: 100%;
                margin-left: initial;
                margin-right: initial;
            }

            @media (max-width: 576px) {
                max-width: 265px;
            }

            @media (max-width: 420px) {
                max-width: 190px;
            }
        }

        &-slide {
            max-width: 100px;
            cursor: pointer;

            img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                object-fit: cover;
            }

            @media (max-width: 1200px) {
                max-width: 80px;
            }

            @media (max-width: 992px) {
                max-width: 65px;
            }

            @media (max-width: 576px) {
                max-width: 45px;
            }

            @media (max-width: 420px) {
                max-width: 30px;
            }
        }
    }

    &__mobile {
        &-inner {
            position: absolute;
            bottom: 60px;
            left: 35px;
            width: 620px;

            @media (max-width: 1200px) {
                left: 0;
            }

            @media (max-width: 1200px) {
                width: 480px;
            }

            @media (max-width: 992px) {
                width: 100%;
                position: initial;
                margin-top: 20px;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }
        }
    }
}