.hero {
    &__title {
        position: absolute;
        left: 0;
        top: 0;
        visibility: hidden;
    }

    &__slider {
        width: 100%;

        & > .swiper-pagination-bullets {
            bottom: 40px;

            @media (max-width: 767px) {
                bottom: 20px;
            }
        }

        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            background-color: var(--clr-default-100);
            opacity: 1;
        }

        .swiper-pagination-bullet-active {
            background-color: var(--clr-primary-500);
        }

        & > .swiper-pagination-bullets {
            .swiper-pagination-bullet + .swiper-pagination-bullet {
                margin-left: 40px;

                @media (max-width: 767px) {
                    margin-left: 15px;
                }
            }
        }
    }

    &__slide {
        padding-top: 120px;
        padding-bottom: 150px;

        &-img {
            position: absolute;
            left: 0;
            top: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &-info {
            margin-left: calc((100% - 1170px) / 2);
            max-width: 450px;

            @media (max-width: 1200px) {
                margin-left: 15px;
            }

            @media (max-width: 576px) {
                margin-right: auto;
                margin-left: auto;
            }
        }

        &-title {
            max-width: 9ch;
            font-weight: 700;
            font-size: fluid-font(6.4rem, 3.6rem);
            line-height: 100%;
            color: var(--clr-primary-500);

            @media (max-width: 576px) {
                max-width: initial;
                text-align: center;
            }
        }

        &-text {
            margin-top: 30px;
            margin-bottom: 55px;
            max-width: 440px;
            line-height: 160%;
            color: rgba(93, 99, 112, 1);

            @media (max-width: 992px) {
                margin-top: 20px;
                margin-bottom: 25px;
            }

            @media (max-width: 576px) {
                max-width: initial;
                text-align: center;
            }
        }

        @media (max-width: 767px) {
            padding: 70px 15px 80px;
        }
    }

    &__btns {
        display: flex;
        align-items: center;

        li + li {
            margin-left: 30px;

            @media (max-width: 1200px) {
                margin-left: 15px;
            }

            @media (max-width: 992px) {
                margin-top: 15px;
                margin-left: initial;
            }
        }

        @media (max-width: 992px) {
            flex-direction: column;
            align-items: initial;
        }

        @media (max-width: 576px) {
            align-items: center;
        }
    }

    &__btn {
        &--blue {
            padding-left: 50px;
            min-width: 230px;
            color: var(--clr-default-100);
            background-color: var(--clr-primary-500);

            @media (hover) {
                &:hover {
                    outline: 2px solid var(--clr-primary-500);
                    color: var(--clr-default-800);
                    background-color: transparent;
                    transition: background-color $ms, color $ms, outline $ms;

                    .hero__btn-img {
                        background-color: var(--clr-primary-500);
                        transition: background-color $ms;

                        svg {
                            fill: var(--clr-default-100);
                            transition: fill $ms;
                        }
                    }
                }
            }
        }

        &--white {
            outline: 2px solid var(--clr-primary-500);
            min-width: 190px;
            color: var(--clr-default-800);

            @media (hover) {
                &:hover {
                    outline: initial;
                    color: var(--clr-default-100);
                    background-color: var(--clr-primary-500);
                    transition: background-color $ms, color $ms, outline $ms;
                }
            }
        }
    }
}
