.footer {
    background-color: var(--clr-default-800);

    &__title {
        margin-bottom: 30px;
        font-size: fluid-font(2.4rem, 2rem);
        line-height: 110%;
        color: var(--clr-default-200);

        @media (max-width: 767px) {
            margin-bottom: 20px;
        }
    }

    &__text {
        max-width: 255px;

        @media (max-width: 767px) {
            max-width: initial;
        }

        p {
            font-size: 1.4rem;
        }
    }

    &__privacy {
        a {
            position: relative;
            display: inline-block;
            transition: color $ms;

            &::before {
                content: "";
                position: absolute;
                left: 50%;
                bottom: -2px;
                width: 0;
                height: 1px;
                background-color: var(--clr-primary-300);
                transform: translateX(-50%);
                transition: width $ms;
            }

            @media (hover) {
                &:hover {
                    color: var(--clr-primary-300);
                    transition: color $ms;

                    &::before {
                        width: 100%;
                        transition: width $ms;
                    }
                }
            }
        }
    }

    &__email {
        color: var(--clr-primary-300);

        position: relative;
        display: inline-block;

        &::before {
            content: "";
            position: absolute;
            left: 50%;
            bottom: -2px;
            width: 0;
            height: 1px;
            background-color: var(--clr-primary-300);
            transform: translateX(-50%);
            transition: width $ms;
        }

        @media (hover) {
            &:hover {
                &::before {
                    width: 100%;
                    transition: width $ms;
                }
            }
        }
    }

    &__social {
        &-list {
            margin-top: 45px;
            margin-bottom: 45px;
            display: flex;
            align-items: center;

            @media (max-width: 992px) {
                margin-top: 10px;
                margin-bottom: 10px;
            }
        }

        &-item {
            transition: transform $ms;

            svg {
                width: 26px;
                height: 26px;
                fill: #5E7AB5;
                transition: fill $ms;
            }

            @media (hover) {
                &:hover {
                    svg {
                        fill: var(--clr-primary-500);
                        transition: fill $ms;
                    }
                }
            }
        }

        &-item + &-item {
            margin-left: 40px;

            @media (max-width: 992px) {
                margin-left: 15px;
            }
        }
    }

    &__copyright {
        margin-right: 15px;

        @media (max-width: 576px) {
            margin-right: initial;
            margin-bottom: 15px;
        }
    }

    &__left {
        margin-right: 15px;

        &-list {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 30px 90px;

            @media (max-width: 992px) {
                grid-template-columns: 1fr;
                grid-gap: 15px 15px;
            }

            @media (max-width: 767px) {
                grid-template-columns: repeat(3, 1fr);
            }

            @media (max-width: 576px) {
                grid-template-columns: repeat(2, 1fr);
            }

            @media (max-width: 420px) {
                display: block;
            }
        }

        &-item {
            a {
                font-size: 1.4rem;
                line-height: 110%;
                transition: color $ms;
                @media (hover) {
                    &:hover {
                        color: var(--clr-primary-300);
                        transition: color $ms;
                    }
                }
            }
        }

        &-item + &-item {
            @media (max-width: 420px) {
                margin-top: 15px;
            }
        }
    }

    &__right {
        &-wrapper {
            display: flex;
            justify-content: space-between;

            @media (max-width: 767px) {
                margin-top: 20px;
                flex-direction: column;
            }

            &-social {
                margin-left: 65px;

                @media (max-width: 767px) {
                    margin-left: initial;
                    margin-top: 20px;
                }
            }
        }

        &-list {
            @media (max-width: 767px) {
                display: grid;
                grid-template-columns: repeat(4, 1fr);
                grid-gap: 15px 15px;
            }

            @media (max-width: 420px) {
                grid-template-columns: repeat(2, 1fr);
            }

            a {
                font-size: 1.4rem;
                line-height: 110%;
                transition: color $ms;
                @media (hover) {
                    &:hover {
                        color: var(--clr-primary-300);
                        transition: color $ms;
                    }
                }
            }
        }

        &-item + &-item {
            margin-top: 30px;

            @media (max-width: 992px) {
                margin-top: 15px;
            }

            @media (max-width: 767px) {
                margin-top: initial;
            }
        }
    }

    &__top {
        padding-top: 80px;
        padding-bottom: 50px;
        color: rgba(95, 108, 135, 1);

        @media (max-width: 767px) {
            padding-top: 40px;
            padding-bottom: 25px;
        }

        &-wrapper {
            display: flex;
            justify-content: space-between;

            @media (max-width: 767px) {
                flex-direction: column;
            }
        }
    }

    &__bottom {
        border-top: 1px solid var(--clr-default-700);
        padding-top: 50px;
        padding-bottom: 50px;
        color: var(--clr-default-700);

        @media (max-width: 767px) {
            padding-top: 25px;
            padding-bottom: 25px;
        }


        &-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;

            @media (max-width: 576px) {
                flex-direction: column;
                text-align: center;
            }
        }
    }
}