.specialists {
    padding-top: 50px;
    padding-bottom: 55px;

    @media (max-width: 767px) {
        padding-top: 25px;
        padding-bottom: 25px;
    }

    &__wrapper {
        position: relative;
        padding-top: 50px;

        @media (max-width: 1200px) {
            padding-top: 100px;
        }

        @media (max-width: 992px) {
            padding-top: initial;
        }
    }

    &__top {
        @media (max-width: 992px) {
            display: flex;
            align-items: start;
            justify-content: space-between;
            margin-bottom: 15px;
        }

        &-inner {
            @media (max-width: 992px) {
                margin-top: 15px;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @media (max-width: 576px) {
                margin-top: initial;
                display: initial;
                margin-right: 15px;
            }
        }

        &-right {
            @media (max-width: 992px) {
                margin-left: 15px;
            }

            @media (max-width: 576px) {
                margin-left: initial;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: row-reverse;
            }
        }
    }

    &__info {
        position: absolute;
        top: 0;
        left: 0;
        max-width: 480px;
        z-index: 2;

        @media (max-width: 992px) {
            position: initial;
        }
    }

    &__text {
        margin-top: 25px;
        max-width: 470px;
        line-height: 160%;
    }

    &__btn {
        position: absolute;
        top: 0;
        right: 0;

        .section__btn-img {
            background-color: var(--clr-default-800);
        }

        @media (max-width: 992px) {
            position: relative;
        }

        &--hide {
            display: none;
        }
    }

    /* SLIDER */

    .swiper-pagination {
        position: initial;
        max-width: 80px;
        display: flex;
        align-items: center;
        margin-left: 25px;

        @media (max-width: 1200px) {
            max-width: 60px;
            margin-left: 15px;
        }

        @media (max-width: 992px) {
            max-width: initial;
            margin-left: initial;
        }

        @media (max-width: 576px) {
            display: none;
        }
    }

    .swiper-pagination-current {
        position: relative;
        margin-right: 16px;
        padding-right: 18px;
        font-weight: 300;
        font-size: fluid-font(3.6rem, 2rem);
        line-height: 130%;
        color: var(--clr-default-700);

        @media (max-width: 1200px) {
            margin-right: 12px;
            padding-right: 14px;
        }

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: 0;
            height: 5px;
            width: 5px;
            border-radius: 50%;
            background-color: #a7b9cd;
            transform: translateY(-50%);
        }
    }

    .swiper-pagination-total {
        font-weight: 300;
        font-size: fluid-font(2.4rem, 1.6rem);
        line-height: 190%;
        color: #a7b9cd;
    }

    .swiper-button-next,
    .swiper-button-prev {
        position: initial;
        border-radius: 50%;
        background-color: var(--clr-primary-500);

        svg {
            fill: var(--clr-default-100);
        }

        @media (max-width: 992px) {
            width: 40px;
            height: 40px;
        }
    }

    .swiper-button-disabled {
        background-color: #2B3E60;
    }

    .swiper-button-prev {
        margin-right: 30px;

        @media (max-width: 1200px) {
            margin-right: 15px;
        }

        @media (max-width: 992px) {
            margin-right: 5px;
        }
    }

    &__inner {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__slider {
        padding-right: 90px;

        @media (max-width: 1200px) {
            padding-right: 70px;
        }

        @media (max-width: 992px) {
            padding-right: initial;
        }

        &-btns {
            display: flex;
            align-items: center;
            position: absolute;
            top: 50%;
            left: 51%;
            transform: translate(-50%, -51%);
            z-index: 1;

            @media (max-width: 992px) {
                position: initial;
                transform: initial;
            }
        }
    }

    &__prof {
        &-list {
            margin-bottom: 20px;
            display: flex;
            align-items: center;
            font-size: fluid-font(1.6rem, 1.4rem);
            color: #5F6C87;

            @media (max-width: 576px) {
                margin-bottom: initial;
            }
        }

        &-item:not(:last-child) {
            position: relative;
            margin-right: 4px;
            padding-right: 12px;

            &::before {
                content: "•";
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
            }
        }
    }

    &__slide {
        display: flex;
        align-items: center;
        justify-content: space-between;
        z-index: 0;

        @media (max-width: 576px) {
            flex-direction: column-reverse;
        }

        &-info {
            max-width: 370px;
            margin-right: 15px;

            @media (max-width: 992px) {
                max-width: 300px;
            }

            @media (max-width: 576px) {
                margin-top: 10px;
                margin-bottom: 15px;
                max-width: initial;
                margin-right: initial;
                display: flex;
                flex-direction: column-reverse;
            }

            .section__btn {
                @media (max-width: 576px) {
                    display: none;
                }
            }
        }

        &-title {
            margin-bottom: 55px;
            font-size: fluid-font(3.6rem, 2.4rem);
            line-height: 130%;
            color: var(--clr-default-800);

            @media (max-width: 992px) {
                margin-bottom: 25px;
            }

            @media (max-width: 576px) {
                margin-bottom: 10px;
            }
        }

        &-img {
            position: relative;
            max-width: 512px;

            @media (max-width: 1200px) {
                max-width: 450px;
            }

            img {
                border-radius: 50%;
                width: 100%;
                height: auto;
                object-fit: cover;
            }

            &-btn {
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                bottom: 80px;

                .section__btn-img {
                    background-color: var(--clr-primary-400);

                    svg {
                        fill: var(--clr-default-100);
                    }
                }

                @media (max-width: 767px) {
                    min-height: 50px;
                    min-width: 200px;
                    bottom: 40px;
                }

                @media (max-width: 576px) {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    bottom: 30px;
                }

                .section__btn--large {
                    min-height: 50px;
                }

                .section__btn--small {
                    @media (max-width: 576px) {
                        margin-top: 10px;
                        background-color: var(--clr-default-100);
                    }
                }

                .specialists__btn--hide {
                    @media (max-width: 576px) {
                        display: initial;
                    }
                }
            }
        }
    }
}