.news {
    padding-top: 40px;
    padding-bottom: 100px;
    background-color: #edf4ff;

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__btn {
        margin-left: 45px;

        .section__btn-img {
            background-color: var(--clr-default-800);
        }

        @media (max-width: 767px) {
            margin-left: 15px;
        }

        @media (max-width: 576px) {
            margin-top: 15px;
            margin-right: auto;
            margin-left: auto;
        }
    }

    &__inner {
        display: flex;
        align-items: center;
        margin-right: 15px;
    }

    &__slider {
        margin-top: 50px;

        &-btns {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .swiper-button-next,
            .swiper-button-prev {
                position: initial;
                border-radius: 50%;
                background-color: var(--clr-primary-500);

                svg {
                    fill: var(--clr-default-100);
                }

                @media (max-width: 992px) {
                    width: 40px;
                    height: 40px;
                }
            }

            .swiper-button-disabled {
                background-color: #2b3e60;
            }

            .swiper-button-prev {
                margin-right: 30px;

                @media (max-width: 1200px) {
                    margin-right: 15px;
                }

                @media (max-width: 992px) {
                    margin-right: 5px;
                }
            }
        }

        @media (max-width: 767px) {
            margin-top: 30px;
        }
    }

    &__slide {
        position: relative;
        transition: box-shadow $ms;

        a {
            display: block;
        }

        &-img {
            position: relative;
            padding-top: 116%;

            &::before {
                content: "";
                position: absolute;
                bottom: 0;
                z-index: 1;
                border-radius: 10px;
                width: 100%;
                min-height: 200px;
                background-image: linear-gradient(180deg, rgba(27, 39, 63, 0) 0%, #0e57ca 100%);
            }

            img {
                position: absolute;
                left: 0;
                top: 0;
                border-radius: 10px;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &-info {
            position: absolute;
            left: 50%;
            bottom: 35px;
            z-index: 1;
            width: 100%;
            max-width: 270px;
            color: var(--clr-default-100);
            transform: translateX(-50%);

            @media (max-width: 992px) {
                bottom: 15px;
                padding-right: 5px;
                padding-left: 5px;
            }
        }

        &-title {
            overflow: hidden;
            margin-bottom: 25px;
            max-height: 75px;
            font-weight: 700;
            font-size: fluid-font(1.8rem, 1.6rem);
            line-height: 135%;
            text-overflow: ellipsis;

            @media (max-width: 992px) {
                margin-bottom: 10px;
            }
        }

        &-date {
            font-size: 1.4rem;
        }

        @media (hover) {
            &:hover {
                box-shadow: 0 0 20px rgba(46, 85, 146, 0.2);
                transition: box-shadow $ms;
            }
        }
    }

    @media (max-width: 767px) {
        padding-bottom: 50px;
    }
}