.service {
    padding-top: 70px;
    padding-bottom: 50px;

    @media (max-width: 767px) {
        padding-top: 40px;
        padding-bottom: 30px;
    }

    &__slider {
        margin-top: 45px;

        &-btns {
            align-items: center;
            display: none;

            @media (max-width: 460px) {
                display: flex;
                margin-top: 20px;
                align-items: center;
                justify-content: center;
            }

            .swiper-button-next,
            .swiper-button-prev {
                position: initial;
                border-radius: 50%;
                background-color: var(--clr-primary-500);

                svg {
                    fill: var(--clr-default-100);
                }

                @media (max-width: 992px) {
                    width: 40px;
                    height: 40px;
                }
            }

            .swiper-button-disabled {
                background-color: #2B3E60;
            }
        }
    }

    &__wrapper {
        position: relative;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            min-width: 170px;
            min-height: 150px;
            opacity: 0.5;
            z-index: 1;
            background-color: var(--clr-default-100);

            @media (max-width: 1200px) {
                display: none;
            }
        }
    }

    .swiper-button-next {
        right: -30px;
        transform: translateY(-50%);
        border-radius: 50%;
        background-color: var(--clr-primary-500);
        transition: box-shadow $ms;

        @media (max-width: 1200px) {
            width: 50px;
            height: 50px;
            right: -15px;
        }

        @media (max-width: 460px) {
            transform: initial;
            margin-left: 20px;
        }

        svg {
            fill: var(--clr-default-100);
        }

        @media (hover) {
            &:hover {
                box-shadow: 0px 5px 20px rgba(27, 39, 63, 0.2);
                transition: box-shadow $ms;
            }
        }
    }

    &__slide {
        padding: 20px 25px;
        min-width: 170px;
        min-height: 150px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: var(--clr-default-100);
        box-shadow: 0px 5px 20px rgba(46, 85, 146, 0.1);
        border-radius: 10px;

        &-img {
            max-width: 26px;
        }

        svg {
            fill: var(--clr-primary-500);
            transition: transform $ms;
        }

        &-title {
            font-size: 1.4rem;
            line-height: 115%;
            text-transform: uppercase;
            color: var(--clr-default-800);
            transition: transform $ms;
        }

        &.swiper-slide-active {
            background-color: var(--clr-primary-500);

            .service__slide-title {
                color: var(--clr-default-100);
                transform: translateY(-20px);
                transition: transform $ms;
            }

            svg {
                fill: var(--clr-default-100);
                transform: translateY(20px);
                transition: transform $ms;
            }
        }
    }

    &__btn {
        @media (max-width: 460px) {
            display: none;
        }
    }
}