$burgerMQ: 992px;
.header {
    padding-top: 25px;
    padding-bottom: 25px;

    @media (max-width: $burgerMQ) {
        padding-top: 15px;
        padding-bottom: 15px;
    }

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
        max-width: 208px;

        img {
            width: 100%;
            height: auto;
            object-fit: cover;
        }

        @media (max-width: $burgerMQ) {
            margin-right: 15px;
        }
    }

    &__nav {
        @media (max-width: $burgerMQ) {
            position: fixed;
            right: -100%;
            top: 0;
            z-index: 20;
            display: flex;
            overflow: auto;
            width: 100%;
            height: 100%;
            max-width: 320px;
            background-color: var(--clr-default-100);
            transition: right $ms;

            &--active {
                right: 0;
                transition: right $ms;
            }
        }

        &-list {
            display: flex;
            align-items: center;

            @media (max-width: $burgerMQ) {
                flex-direction: column;
                margin: auto;
                padding-block: 40px;
            }
        }

        &-item {
            &--hide {
                display: none;

                @media (max-width: $burgerMQ) {
                    display: initial;
                }
            }
        }

        &-item + &-item {
            margin-left: 40px;

            @media (max-width: 1200px) {
                margin-left: 15px;
            }

            @media (max-width: $burgerMQ) {
                margin-left: initial;
                margin-top: 30px;
            }
        }

        &-link {
            position: relative;
            display: block;

            &::before {
                content: "";
                position: absolute;
                left: 50%;
                bottom: 0;
                width: 0;
                height: 1px;
                background-color: var(--clr-default-800);
                transform: translateX(-50%);
                transition: width $ms;
            }

            @media (hover) {
                &:hover {
                    &::before {
                        width: 100%;
                        transition: width $ms;
                    }
                }
            }

            @media (max-width: $burgerMQ) {
                font-size: fluid-font(2.4rem, 2.2rem);
            }
        }
    }

    &__dropdown {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-left: 15px;
        margin-right: 15px;

        @media (max-width: $burgerMQ) {
            display: none;
        }

        &-btn {
            margin-right: 20px;
            position: relative;
            display: block;
            z-index: 15;
            width: 22px;
            height: 14px;
            background-color: transparent;
            &::after,
            &::before,
            span {
                content: "";
                position: absolute;
                left: 0;
                width: 100%;
                height: 2px;
                border-radius: 5px;
                background-color: var(--clr-primary-500);
            }

            &::before {
                top: 0;
                transition: transform $ms, top $ms;
            }
            &::after {
                width: 13px;
                bottom: 0;
                transition: transform $ms, bottom $ms;
            }
            span {
                top: 6px;
                transition: transform $ms;
            }
        }
    }

    &__map {
        margin-left: 15px;
        margin-right: 15px;
        position: relative;
        width: 50px;
        height: 50px;
        background-color: var(--clr-primary-300);
        border-radius: 50%;
        transition: outline $ms, background-color $ms;

        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            fill: var(--clr-default-100);
            transition: fill $ms;
        }

        @media (hover) {
            &:hover {
                outline: 1px solid var(--clr-primary-300);
                background-color: var(--clr-default-100);
                transition: outline $ms, background-color $ms;

                svg {
                    fill: var(--clr-primary-300);
                    transition: fill $ms;
                }
            }
        }

        @media (max-width: $burgerMQ) {
            margin-left: initial;
            margin-right: initial;
        }
    }

    .btn {
        @media (max-width: $burgerMQ) {
            display: none;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        border-radius: 50px;
        padding-left: 15px;
        padding-right: 15px;
        min-height: 50px;
        min-width: 190px;
        color: var(--clr-default-100);
        background-color: var(--clr-primary-500);
        transition: box-shadow $ms;

        @media (hover) {
            &:hover {
                box-shadow: 0px 5px 20px rgba(27, 39, 63, 0.2);
                transition: box-shadow $ms;
            }
        }
    }

    &__burger {
        $width: 25px;
        $height: 20px;
        $bgColor: var(--clr-primary-500);
        $burgerLineHeight: 4px;

        display: none;

        @media (max-width: $burgerMQ) {
            position: relative;
            z-index: 21;
            display: block;
            width: $width;
            height: $height;
            background-color: transparent;

            &::after,
            &::before,
            span {
                content: "";
                position: absolute;
                left: 0;
                width: 100%;
                height: $burgerLineHeight;
                background-color: $bgColor;
            }

            &::before {
                top: 0;
                transition: transform $ms, top $ms;
            }
            &::after {
                bottom: 0;
                transition: transform $ms, bottom $ms;
            }
            span {
                top: 8px;
                transition: transform $ms;
            }

            &--active {
                span {
                    transform: scale(0);
                    transition: transform $ms;
                }
                &::before {
                    top: 8px;
                    transform: rotate(45deg);
                    transition: transform $ms, top $ms;
                }
                &::after {
                    bottom: 8px;
                    transform: rotate(-45deg);
                    transition: transform $ms, bottom $ms;
                }
            }
        }
    }

    &--fixed {
        position: fixed;
        z-index: 11;
        min-height: 70px;
        background-color: var(--clr-default-100);
        width: 100%;
        transition: $ms;
    }

}
